<template lang="pug">
	CPage.page-board.board
		nav.board__nav
			//- router-link.board__nav-link(to="/store/new") Создать торговую площадку
			router-link.board__nav-link(to="/store/new") Add new shop
			//- router-link.board__nav-link(to="/new") Мои заказы
			//- router-link.board__nav-link(to="/new") Добавить
			//- router-link.board__nav-link(to="/new") Найти
			//- router-link.board__nav-link(to="/new") Профиль
			button.board__nav-link(@click="clickHandler") Exit

		h2.board__title My Shops ({{ myStores ? myStores.length : '0' }})
		.board__my-shops(v-if="myStores" v-for="myStore in myStores")
			CStore.board__my-shop(:store="myStore" :key="myStore.id")
			router-link.board__my-shop-edit(:to="'/store/' + myStore.id + '/edit'") Edit shop
		.board__ads-empty(v-else)
			span.board__text Пусто.
			router-link(to="/add") Добавить
</template>

<script>

export default {
	name: 'Board',
	data() {
		return {
			myStores: null
		}
	},
	components: {
		CStore: () => import('@/components/App/CStore')
	},
	mounted() {
		// console.log(this.$lang('title'))
		this.$req('stores?isOwner', 'GET')
			.then(res => {
				// console.log(res)
				this.myStores = res.stores
			})
	},
	methods: {
		clickHandler() {
			try {
				this.$req('auth?logout', 'GET')
				.then(() => {
					// this.$store.commit('clearAuth');
					// // console.log(localStorage.getItem('user'))
					// this.$router.go();
					this.logout()
				})
				.catch(() => {
					this.logout()
				})
			} catch(e) {
				console.error(e);
				this.logout()
				// this.$store.commit('clearAuth');
				// this.$router.go();
			}
		},
		logout() {
			this.$store.commit('clearAuth');
			this.$router.push('/?msg=leave');
			// this.$router.go('/');
		}
	}
}
</script>

